.switch-list {
  height: 100vh;
  width: 100vw;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  display: flex;
}

.switch {
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  align-items: center;
  display: flex;
}

.switch__input {
  display: none;
}

.switch__input:checked + .switch__icon {
  background: linear-gradient(#4f2ee8, #686868);
}

.switch__input:checked + .switch__icon:before {
  transform: translate3d(18px, 2px, 0)scale3d(0, 0, 0);
}

.switch__input:checked + .switch__icon:after {
  transform: translate3d(20px, 2px, 0);
}

.switch__icon {
  max-width: 42px;
  min-width: 42px;
  min-height: 24px;
  max-height: 24px;
  vertical-align: text-bottom;
  background-color: #bebebe;
  border-radius: 22px;
  transition: all .3s linear;
  display: inline-block;
  position: relative;
}

.switch__icon:before {
  content: "";
  width: 38px;
  height: 20px;
  background: linear-gradient(#4f2ee8, #dcdcdc);
  border-radius: 11px;
  transition: all .25s linear;
  position: absolute;
  left: 0;
  transform: translate3d(2px, 2px, 0)scaleX(1);
}

.switch__icon:after {
  content: "";
  width: 20px;
  height: 20px;
  background-color: #fff;
  border-radius: 11px;
  transition: all .2s ease-in-out;
  position: absolute;
  left: 0;
  transform: translate3d(2px, 2px, 0);
  box-shadow: 0 2px 2px rgba(0, 0, 0, .24);
}

