.js-backdrop-modal {
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(17, 17, 17, 0.4);
    z-index: 2;
    overflow: auto;
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1), visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);

    &.is-hidden {
        z-index: -2;
        display: none;
    }
}

.modal-conteiner {
    width: 375px;
    padding-left: 20px;
    padding-right: 20px;

    @include tablet {
        width: 768px;
        padding-left: 32px;
        padding-right: 32px
    }
}

.modal {
    position: fixed;
    z-index: 12;
    width: 50%;
    background: var(--background-white);
    opacity: 1;
    transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
    transform: translate(-50%, -50%);
    transition: 0.4s ease-in-out;

    &.is-hidden {
        opacity: 0;
        pointer-events: none;
        visibility: hidden;
    }
}



.about-book-modal {
    width: 335px;
    background-color: #f6f6f6;
    border: 2px solid #fff;
    border-radius: 18px;
    padding: 40px 24px;
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 400;
    display: inline-block;
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    @include tablet {
        width: 579px;
        padding: 40px
    }
}

.image-modal {
    width: 100%;
    height: 100%;
    object-fit: fill;
    border-radius: 8px;
    height: 408px;
    background-color: inherit;
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: 60px 54px;
    border-radius: 8px;
    overflow: hidden;

    @include tablet {
        width: 192px;
        height: 281px;
        background-size: 88px 80px;
        margin-right: 24px;
    }
}

.info-modal {
    @include tablet {
        margin-top: -14px;
    }
}

.close-modal-btn {
    position: absolute;
    top: 12px;
    right: 12px;
    background: none;
    border: none;
    cursor: pointer;
    padding: 0;
    width: 24px;

    @include tablet {
        top: 24px;
        right: 24px;
    }

}


.modal-card-wr {
    @include tablet {
        width: 495px;
        height: 281px;
        flex-flow: column wrap;
        display: flex;
    }
}

.book-info-modal {
    margin-top: 14px;
    display: block;

    @include tablet {
        width: 279px;
        max-height: 219px;
        margin-top: 0;
        overflow: auto;

    }
}

.cont-title {
    @include mobile {
        width: 128px;
    }

    @include tablet {
        width: 192px;
    }
}

.title-book-modal {
    letter-spacing: -.04em;
    color: var(--text-color-main);
    font-family: 'DM Sans';
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 18px;
    margin-top: 14px;

    @include tablet {
        font-size: 24px;
        line-height: 1.17;
    }
}

.author-book-modal {
    font-family: 'DM Sans';
    font-style: italic;
    font-size: 12px;
    line-height: 14px;
    color: #b4afaf;
    margin-top: 4px;
    width: 287px;
    font-size: 14px;
    line-height: 18px;
    @include tablet {
        margin-top: 8px;
        font-size: 14px;
        line-height: 1.29;
    }
}

.txt-book-modal {
    font-family: 'DM Sans';
    width: 287px;
    font-size: 14px;
    line-height: 18px;
    margin-top: 18px;
    @include tablet {
        margin-top: 20px;
    }
}

.modal-shopping-list {
    align-items: center;
    gap: 20px;
    margin-top: 14px;
    display: flex;
    gap: 20px;
    justify-content: start;

    &-amazon {
        width: 62px;
        height: 19px; 
        
        &:hover {mix-blend-mode: luminosity;
        filter: drop-shadow(0px 3px 3px rgba(0, 0, 0, 0.15));}   
    }
        

    &-apple {
        width: 32px;
        height: 32px;

        &:hover {mix-blend-mode: luminosity;
        filter: drop-shadow(0px 3px 3px rgba(0, 0, 0, 0.15));}  
    }

    &-book {
        width: 38px;
        height: 36px;
        
        &:hover {mix-blend-mode: luminosity;
        filter: drop-shadow(0px 3px 3px rgba(0, 0, 0, 0.15));}  
    }

    @include tablet {
        margin-top: 20px;
    }
}


.add-storage-button {
    width: 221px;
    letter-spacing: -.01em;
    text-transform: uppercase;
    color: var(--text-color-main);
    box-sizing: border-box;
    text-align: center;
    background-color: rgba(0, 0, 0, 0);
    border: 2px solid #4f2ee8;
    border-radius: 40px;
    margin: 40px auto auto;
    padding: 14px 24px;
    font-family: 'DM Sans';
    font-size: 14px;
    font-style: normal;
    font-weight: 700;
    line-height: 1.29;
    transition: background-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
    display: block;

    &:hover {
        background-color: var(--accent-primary);
        color: var(--background-white);
    }

    &:focus {
        background-color: var(--accent-primary);
        color: var(--background-white);
    }

    @include tablet {
        width: 100%;
        padding-top: 20px;
        padding-bottom: 20px;
        font-size: 18px;
        line-height: 1.33;
    }
}

